<template>
  <div class="row">
    <div class="col-md-8">
      <div class="noProjects" v-show="noProjects">
          <h4> Oops! You have no Projects to view. </h4>
          <button class="btn btn-primary btn-micro" @click="createNew"> New project</button>              
      </div>
      <vuestic-widget class="" v-for="project in projectArray" :key="project.id">
        <div>
          <div id="projects-name-div">
            <span class="projects-name"><strong><a href="#" @click="viewProject(project._id)">{{project.title}}</a> </strong></span><br>
            <span class="projects-time"><timeago :since="project.createdAt" :auto-update="60"></timeago></span>
          </div>
          <div class="deleteIconDiv" @click="showDeleteModal(project._id)">
            <i class="fa fa-trash deleteIcon"></i>
          </div>
        </div>

        <div id="projects-content-div">
          <span id="projects-description">{{project.abstract}}</span>
          <a href="" class="viewMoreBtn" @click="viewProject(project._id)"> Read More <i class="fa fa-arrow-right"></i> </a>          
        </div>
        <div id="tagDiv">
          <strong>Tags:</strong><span v-for="tag in project.tags" :key="tag.id" class="tagNames">{{tag.name}}</span>
        </div>
        
      </vuestic-widget>
    </div>
    <div class="col-md-4">
      <vuestic-widget class="createproject-div">
        <div class="col-md-offset-6 col-md-12">
          <h5 class="gotnew">Got something new?</h5>
          <button class="btn btn-primary btn-micro" @click="createNew"> New project</button>    
        </div>
      </vuestic-widget>
      <vuestic-widget class="live-feed" headerText="Live feeds">
        <vuestic-feed class="newsfeed-page" :initialPosts="posts"></vuestic-feed>
      </vuestic-widget>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'projectList',
    component: {},
    data () {
      return {
        projectArray: [],
        noProjects: false,
        authToken: this.$ls.get('token'),
        posts: [
          {
            id: 0,
            photoURL: 'https://goo.gl/KnVxVY',
            name: 'Harishankar',
            text: 'is going for a contest'
          },
          {
            id: 1,
            photoURL: 'https://goo.gl/1nKusR',
            name: 'Balaji D Loganathan',
            text: 'upvoted for a project'
          },
          {
            id: 2,
            photoURL: 'https://goo.gl/Ckaexc',
            name: 'Surendran S',
            text: 'upvoted for a project'
          }
        ]
      }
    },

    methods: {
      createNew: function () {
        this.$router.push('/student/projects/new')
      },
      viewProject: function (projectId) {
        this.$router.push('/student/project/' + projectId)
      },
      showDeleteModal: function (projectId) {
        const self = this
        const authToken = this.$ls.get('token')
        this.$swal({
          title: 'Are you sure?',
          text: "You won't be able to revert this!",
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#BA1F33',
          cancelButtonColor: '#4ae387',
          confirmButtonText: 'Yes, delete it!'
        }).then(() => {
          console.log('auth token', self.authToken)
          self.$http.delete('/projects/delete', {
            'project': projectId
          },
            {
              headers: {
                'Authorization': 'Bearer ' + authToken,
                'Content-Type': 'application/json'
              }
            }
          )
          .then(function (projectDeleted) {
            console.log('project delete', projectDeleted)
            self.$swal(
              'Deleted!',
              'Your file has been deleted.',
              'success'
            )
          })
          .catch(function (projectDeleteErr) {
            console.log('projectDeleteErr', projectDeleteErr)
          })
        })
      }
    },
    created () {
      const email = this.$ls.get('email')
      const lsToken = this.$ls.get('token')
      this.$http.get('/students/get?email=' + email, {
        headers: {
          'Authorization': 'Bearer ' + lsToken
        }
      })
      .then((studentData) => {
        console.log('student Data', studentData.data)
        const projectArr = studentData.data[0].projects
        // console.log('projectArr', projectArr)
        if (projectArr.length === 0) {
          this.noProjects = true
        }
        projectArr.map(project => {
          console.log('single project', project)
          this.$http.get('/projects/get?id=' + project, {
            headers: {
              'Authorization': 'Bearer ' + lsToken
            }
          })
          .then((projectData) => {
            // console.log('project data', projectData)
            this.projectArray.push(projectData.data)
            console.log('project array', this.projectArray)
          })
          .catch((projectErr) => {
            console.log('project err', projectErr)
          })
        })
      }).catch((studentErr) => {
        console.log('student err', studentErr)
      })
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../sass/_variables.scss";

  .createproject-div{
    text-align: center;
  }
  .newsfeed-page{
    padding-left: 0rem !important;
  }
  .projects-time{
    margin-top: 3px;
    color: #a29e9e;
  }

  #projects-name-div{
    display: inline-block;
    // margin-left: 0.4rem;
  }

  #projects-content-div{
    margin-top: 0.5rem;
  }

  .gotnew{
    margin-bottom: 1.5rem;
  }
  .noProjects {
    text-align: center;
    font-weight: bold;
    margin-top: 7rem;
  }
  #tagDiv {
    display: inline-block;
    margin-top: 1rem;
  }
  .tagNames {
    padding: 0.2rem 0.5rem;
    margin-left: 0.5rem;
    background: $tagcolor;
    color: white;
    border-radius: 5%;
  }

  .viewMoreBtn {
    display: block;
  }
  .deleteIconDiv {
    float: right;
    cursor: pointer;
  }
  .deleteIcon {
    font-size: 1.2rem;
  }
</style>



